.list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 0;
    padding-left: 50px;
    padding-right: 30px;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}

.img {
    width: 200px;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.heroTitle {
    text-align: center;
}

.title {
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    font-size: 20px;
}


.desc {
    text-align: center;
}

.item {
    width: 200px;
}

.item:hover {
    transform: scale(1.03);
}

.link {


    text-decoration: none;
}

.addInfo {
    display: flex;
    justify-content: space-evenly;
}

.first {
    color: orangered;
}

.secondaryTitle {
    text-align: center;
}

.addBox {
    padding-bottom: 50px;
}